<template>
  <div id="header">
    <!-- web -->
    <div class="d-none d-mb-block">
      <!-- LOGO -->
      <router-link to="/">
        <img class="pos-logo" src="../assets/NTUA_LOGO.svg" alt="NTUA_LOGO" />
      </router-link>
      <!-- MENU -->
      <div>
        <img
          class="pos-menu"
          src="../assets/images/logo/logo_header_1.png"
          alt="menu"
        />
        <img
          v-show="!scrollLeftTop"
          class="pos-menuText"
          src="../assets/images/logo/logo_header_2.png"
          alt="Crafts & Design Department"
        />
        <img
          class="pos-search"
          src="../assets/images/icon/search.png"
          alt="search_btn"
          @click="openSearch()"
        />
        <img
          class="pos-menuList"
          src="../assets/images/icon/menuList.png"
          alt="menuList_btn"
          @click="openMenu()"
        />
      </div>
      <!-- left bar -->
      <div class="leftBar">
        <img src="../assets/images/icon/leftBar.png" alt="" />
        <div class="d-flex align-items-center flex-column" style="width: 60px">
          <span class="cur-pointer">
            <img
              class="mt-11"
              src="../assets/images/icon/FB_icon_1.png"
              alt="Facebook"
            />
          </span>
          <span class="cur-pointer">
            <img
              class="mt-22"
              src="../assets/images/icon/ig_icon_1.png"
              alt="Instagram"
            />
          </span>
          <span class="cur-pointer">
            <img
              class="mt-22"
              src="../assets/images/icon/YT_icon_1.png"
              alt="Youtube"
            />
          </span>
        </div>
      </div>
      <!-- search -->
      <div
        class="searchBox d-flex align-items-center justify-content-center flex-column"
        v-if="clickSearch"
        @mouseleave="hiddenSearch()"
      >
        <el-input v-model="getKeyword" class="input-with-select" clearable>
          <el-button slot="append" icon="el-icon-search"></el-button>
        </el-input>
        <div class="w-100 mt-10">
          <span
            class="referWord cur-pointer"
            v-for="(item, index) in referWord"
            :key="'RW__' + index"
            @click="selectKeyword(item)"
            >{{ item.name }}</span
          >
        </div>
      </div>
      <!-- menu -->
      <div
        class="circleMenu d-flex align-items-center justify-content-between flex-row"
        v-if="clickOpen"
      >
        <div style="width: 40%">
          <ul
            class="w-100 m-0 p-0 d-flex align-items-center justify-content-center flex-column list-none"
          >
            <li
              :class="{ stayPage: item.mainPath == $route.meta.mainPage }"
              v-for="(item, index) in getMenu.leftMenu"
              :key="'GLM__' + index"
              @click="goNextPage(item)"
            >
              {{ item.name }}
            </li>
          </ul>
        </div>
        <div style="width: 60%">
          <ul
            class="w-100 m-0 p-0 d-flex align-items-center justify-content-center flex-column list-none"
          >
            <li
              :class="{ stayPage: item.mainPath == $route.meta.mainPage }"
              v-for="(item, index) in getMenu.rightMenu"
              :key="'GRM__' + index"
              @click="goNextPage(item)"
            >
              {{ item.name }}
            </li>
          </ul>
        </div>
      </div>
    </div>

    <!-- phone -->
    <div class="d-block d-mb-none">
      <div class="phoneScreen">
        <div
          class="px-30 py-20 d-flex align-items-center justify-content-between"
        >
          <router-link to="/">
            <img src="../assets/NTUA_LOGO.svg" alt="NTUA_LOGO" width="225px" />
          </router-link>
          <img
            src="../assets/images/icon/menuList-phone.png"
            alt="menu"
            @click="openMenuModal"
          />
        </div>
      </div>
    </div>

    <!-- phoneModal -->
    <div class="modal" v-if="menuModal">
      <div class="modal-content">
        <div class="w-100 d-flex align-items-center">
          <div class="menuList w-100">
            <ul class="w-100 m-0 p-0 d-flex justify-content-center flex-column">
              <li class="px-10" v-for="item in menuList" :key="item.value">
                {{ item.text }}
              </li>
            </ul>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      scrollTop: "",
      scrollLeftTop: false,
      clickSearch: false,
      clickOpen: false,
      getMenu: {
        leftMenu: [
          {
            mainPath: "departmentMember",
            pathName: "fullTime",
            name: "系所成員",
          },
          {
            mainPath: "alumni",
            pathName: "alumni",
            name: "系友專區",
          },
          {
            mainPath: "",
            pathName: "",
            name: "捐款專區",
          },
          {
            mainPath: "relatedLink",
            pathName: "relatedLink",
            name: "相關連結",
          },
        ],
        rightMenu: [
          {
            mainPath: "latestNews",
            pathName: "bulletin",
            name: "最新消息",
          },
          {
            mainPath: "aboutUS",
            pathName: "history",
            name: "系所簡介及課程",
          },
          {
            mainPath: "",
            pathName: "",
            name: "系所規定與申請表",
          },
          {
            mainPath: "",
            pathName: "",
            name: "招生資訊",
          },
          {
            mainPath: "teachingResult",
            pathName: "award",
            name: "教學成果",
          },
          {
            mainPath: "website",
            pathName: "workCamp",
            name: "研討會/工作營網站",
          },
        ],
      },
      getKeyword: "",
      referWord: [
        {
          name: "轉學考",
        },
        {
          name: "教學設備",
        },
        {
          name: "關鍵字關鍵",
        },
      ],
      menuModal: false,
      menuList: [
        {
          value: "01",
          text: "最新消息",
        },
        {
          value: "02",
          text: "系所簡介及課程",
        },
        {
          value: "03",
          text: "系所規定與申請表",
        },
        {
          value: "04",
          text: "招生資訊",
        },
        {
          value: "05",
          text: "教學成果",
        },
        {
          value: "06",
          text: "研討會/工作營網站",
        },
        {
          value: "07",
          text: "系所成員",
        },
      ],
    };
  },
  methods: {
    handleScroll() {
      this.scrollTop =
        window.pageYOffset ||
        document.documentElement.scrollTop ||
        document.body.scrollTop;
    },
    openSearch() {
      this.clickSearch = !this.clickSearch;
    },
    hiddenSearch(data) {
      this.clickSearch = false;
    },
    openMenu() {
      this.clickOpen = !this.clickOpen;
    },
    selectKeyword(item) {
      this.getKeyword = item.name;
    },
    goNextPage(data) {
      this.$router.push({ name: data.pathName });
      this.clickOpen = false;
    },
    openMenuModal() {
      this.menuModal = !this.menuModal;
    },
  },
  mounted() {
    window.addEventListener("scroll", this.handleScroll);
  },
  watch: {
    scrollTop(val) {
      if (this.scrollTop > 200) {
        this.scrollLeftTop = true;
      } else {
        this.scrollLeftTop = false;
      }
    },
  },
};
</script>

<style lang="scss">
#header {
  z-index: 9999;
  .pos {
    &-logo {
      position: fixed;
      top: 0%;
      left: 2rem;
      z-index: 99;
    }
    &-menu {
      position: fixed;
      top: 0;
      right: 0;
      z-index: 99;
    }
    &-menuText {
      position: fixed;
      top: 0;
      right: 0;
      z-index: 99;
      margin-top: 200px;
      margin-right: 50px;
    }
    &-search {
      position: fixed;
      top: 3rem;
      right: 14rem;
      z-index: 99;
      cursor: pointer;
    }
    &-menuList {
      position: fixed;
      top: 2.5rem;
      right: 6rem;
      z-index: 99;
      cursor: pointer;
    }
  }
  .searchBox {
    position: fixed;
    z-index: 99;
    margin-top: 2.3rem;
    margin-right: 13rem;
    right: 0;
    width: 300px;
    height: 120px;
    .el-input-group__append {
      background: white;
      border: 0;
    }
    .referWord {
      background: #c4c4c4;
      opacity: 0.5;
      color: #52505a;
      border-radius: 10px;
      padding: 2px 10px;
      margin-right: 25px;
      white-space: nowrap;
      line-height: 2rem;
      &:last-child {
        margin-right: 0;
      }
      &:hover {
        opacity: 1;
      }
    }
  }
  .circleMenu {
    position: fixed;
    z-index: 99;
    margin-top: 6rem;
    margin-right: 10rem;
    right: 0;
    width: 470px;
    height: 470px;
    border-radius: 50%;
    background-color: rgba(89, 86, 86, 0.8);
    div > ul > li {
      font-size: 24px;
      line-height: 28px;
      color: #ded9d5;
      margin-bottom: 18px;
      cursor: pointer;
      &:hover {
        background: white;
        color: #ceb87f;
      }
    }
    .stayPage {
      background: white;
      color: #ceb87f;
    }
  }
  .leftBar {
    position: fixed;
    z-index: 99;
    margin-top: 7rem;
    left: 1rem;
  }

  // phone
  .phoneScreen {
    position: fixed;
    z-index: 5;
    width: 100%;
    background: #ffffff;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    border-radius: 0px 0px 10px 10px;
  }

  // modal
  .modal {
    position: fixed;
    z-index: 4;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: rgba(0, 0, 0, 0.8);

    &-content {
      position: fixed;
      bottom: 30%;
      width: 100%;
      animation-name: slideIn;
      animation-duration: 0.5s;
      .menuList {
        writing-mode: vertical-lr;
        color: white;
        ul {
          font-size: 24px;
          font-weight: bold;
          line-height: 28px;
          color: #ded9d5;
          list-style-type: korean-hanja-informal;
        }
      }
    }
  }

  @keyframes slideIn {
    from {
      bottom: 0;
      opacity: 0;
    }

    to {
      bottom: 30%;
      opacity: 1;
    }
  }
}
</style>